import React, {useState} from 'react'
import styled from 'styled-components';
import wallpaper from './wall.jpg'
import Axios from 'axios';
import ButtonLoader from './ButtonLoader'
import { useHistory } from 'react-router';
import {
    BrowserRouter as Router,
    
    
  } from "react-router-dom";
// import {CircleLoader} from 'react-spinners';



function Loginfo() {
    let history = useHistory();
    



    const url = "http://localhost:3000/posts";
    const [data, setData] = useState({
        name: "",
        email: "",
        phoneNumber: "",
        noOfSingers: "",
        noOfNotarizations: ""

    });
    function submit(e){
            e.preventDefault();
            
            Axios.post(url,{
                name: data.name,
                email: data.email,
                phoneNumber: data.phoneNumber,
                noOfSingers: data.noOfSingers,
                noOfNotarizations: data.noOfNotarizations
            }).then(res =>{
               
                
            })
             setTimeout(function(){
                            history.push("/second");
                         }, 2000);
             
    }
    function handle(e){
        const newdata = {...data}
        newdata[e.target.id] = e.target.value
        setData(newdata)
        console.log(newdata)
    }

    return (
        <Router>
        <Container>
            <Left>
                <form onSubmit={(e) => submit(e)}>
                    <h3>Schedule an Online Notary Now </h3>
                    <p>Full Name</p>
                    <input onChange={(e) => handle(e)} id="name" value={data.name} type="text" placeholder="Input Text With Label" required="required"/>
                    <p>Email</p>
                    <input onChange={(e) => handle(e)} id="email" value={data.email} type="text" placeholder="Input Text With Label" required="required"/>
                    <p>Phone Number</p>
                    <input onChange={(e) => handle(e)} id="phoneNumber" value={data.phoneNumber} type="text" placeholder="Input Text With Label" required="required"/>
                    
                    <Selectform>
                        
                        <p>How Many Notarizations</p>
                    
                    <select onChange={(e) => handle(e)} name="Notarizations" id="noOfNotarizations" value={data.noOfNotarizations}>
                        <option value="select" id="select">select</option>
                        <option value="1" id="1">1</option>
                        <option value="2" id="2">2</option>
                        <option value="3" id="3">3</option>
                        <option value="4" id="4">4</option>
                        <option value="5" id="5">5</option>
                        <option value="6" id="6">6</option>
                        <option value="7" id="7">7</option>
                        <option value="8" id="8">8</option>
                    </select>
                    </Selectform>
                    <Selectform>
                    <p>How Many Sinngers</p>
                    <select onChange={(e) => handle(e)} name="singers" id="noOfSingers" value={data.noOfSingers}>
                        <option value="select" id="select">select</option>
                        <option value="1" id="1">1</option>
                        <option value="2" id="2">2</option>
                        <option value="3" id="3">3</option>
                        <option value="4" id="4">4</option>
                        <option value="5" id="5">5</option>
                        <option value="6" id="6">6</option>
                        <option value="7" id="7">7</option>
                        <option value="8" id="8">8</option>
                    </select>
                    </Selectform>
                    <save>
                    <button onClick={submit}>
                    <ButtonLoader />
                    </button>
                    </save>
                    
                </form>
            </Left>
            <Right>

            </Right>
        </Container>
        </Router>
    )
}

export default Loginfo
const Container = styled.div`
display: flex;
height: 100vh;
`;
const Left  = styled.div`
flex: 0.5;
form{
    display: flex;
    flex-direction: column;
    margin: 0 3rem 0 3rem;
}

h3{
    display: flex;
    justify-content: flex-start;
}
p{
    display: flex;
    justify-content: flex-start;
    font-weight: 500;
    color: grey;
}
input{
    height: 1.5rem;
    border: none;
    outline: none;
    border-bottom: 0.5px solid grey;
    margin-bottom: 0.5rem;
}
button{
    margin-top: 60px;
    background: rgba(49,119,108,1);
    color: white;
    height: 2.5rem;
    width: 10rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    border: none;
    cursor: pointer;
}
save{
    display: flex;
    justify-content: flex-end;
}
`;
const Right = styled.div`
flex: 0.5;
background-image: url(${wallpaper});
background-repeat: no-repeat;
background-size: 100% 100%;
`;
const Selectform = styled.div`
display: flex;
justify-content: space-between;
align-items: center;
select{
    width: 8.5rem;
    height: 2rem;
    
    
    
    
    option{
    display: flex;

}
}
p{
    color: rgba(0,0,0,0.8);
}


`;